export class Subject {

    /**
     * @public
     */
    observers = [];

    /**
     * @public
     */
    silent = false;

    /**
     * Subscribe to observer
     *
     * @param {Observer} obeserver
     */
    subscribe(observer) {
        this.observers.push(observer);

        return this;
    }

    /**
     * Unsubscribe from specific observer
     *
     * @param {Observer} observer
     */
    unsubscribe(observer) {
        var index = this.observers.indexOf(observer);
        if(index > -1) {
            this.observers[index].off(this);
            this.observers.splice(index, 1);
        }
    }

    /**
     * Unsubscribe from all observers
     */
    unsubscribeAll() {
        this.observers.forEach(observer => {
            observer.off(this);
        });

        this.observers = [];
    }

    /**
     * Emit an event to notify the observer
     *
     * @param {string} event
     * @param {any[]} args
     */
    emit(event, ...args) {
        if(this.silent) {
            return;
        }

        this.observers.forEach(observer => {
            /**
             * @type {Observer}
             */
            observer.notify(event, this, ...args);
        });
    }

    mute() {
        this.silent = true;

        return this;
    }

    unmute() {
        this.silent = false;

        return this;
    }
}