import { h, Component } from 'preact';
import { v4 as uuid } from "uuid";
import { DATASET_NAMESPACE } from "../constants";

export class Item extends Component {
    key;

    constructor(props, state) {
        super(props, state);

        this.key = uuid();
        this.keyAttribute = `data-${DATASET_NAMESPACE}-html-child`;
    }

    destroyHandler = e => {
        this.props.onDestroy(this.props.id)
    }

	render(props) {
        return null;
    }
}