import { Group } from "../Group";
import { Model } from "../../nodes";

export class Match extends Group {

    constructor(name) {
        super(name);

        this.on('deactivated.all', node => this.onDeactivate(node, false, [node]));
        this.on('activated.all', node => this.onActivate(node, false, [node]));
        this.on('synchronize', (node, state = {}) => {
            if(state.hasOwnProperty('emittedFrom') && 'group' === state.emittedFrom) {
                return;
            }

            this.onPersist(node, false, [node])
        });
        this.on('reset', (node, state = {}) => {
            if(state.hasOwnProperty('emittedFrom') && 'group' === state.emittedFrom) {
                return;
            }

            this.onReset(node, false, [node]);
        });
        this.on('clear', (node, state = {}) => {
            if(state.hasOwnProperty('emittedFrom') && 'group' === state.emittedFrom) {
                return;
            }

            this.onClear(node, false, [node]);
        });
    }

    /**
     * Persist handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onPersist(emitter, silent = false, skip = []) {
        this.members.forEach(id => {
            if(id === emitter.id || !this.container.has(id)) {
                return;
            }

            let node = this.container.get(id);
            if(node === emitter || skip.indexOf(node) !== -1 || skip.indexOf(id) !== -1 ) {
                return;
            }

            node.mutate(emitter.value, emitter.isActive, true, { emittedFrom: 'group' });
        });
    }

    /**
     * Deactivate handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onDeactivate(emitter, silent = false, skip = []) {
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            let node = this.container.get(id);
            if(node === emitter || skip.indexOf(node) !== -1 || skip.indexOf(id) !== -1 || !node.isActive ) {
                return;
            }

            node.deactivate(silent, { emittedFrom: 'group', sync: true });
        });
    }

    /**
     * Activate handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onActivate(emitter, silent = false, skip = []) {
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            let node = this.container.get(id);
            if(node === emitter || skip.indexOf(node) !== -1 || skip.indexOf(id) !== -1 || node.isActive) {
                return;
            }

            node.activate(silent, { emittedFrom: 'group', sync: true });
        });
    }

    /**
     * Reset handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onReset(emitter, silent = false, skip = []) {
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            let node = this.container.get(id);
            if(node === emitter || skip.indexOf(node) !== -1 || skip.indexOf(id) !== -1 ) {
                return;
            }

            node.reset(silent, { emittedFrom: 'group' });
        });
    }

    /**
     * Activate handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onClear(emitter, silent = false, skip = []) {
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            let node = this.container.get(id);
            if(node === emitter || skip.indexOf(node) !== -1 || skip.indexOf(id) !== -1 ) {
                return;
            }

            node.clear(silent, { emittedFrom: 'group' });
        });
    }

    /**
     * @inheritDoc
     */
    getActive() {
        let defaultModel = this.getDefault();
        if(defaultModel && defaultModel.isActive) {
            return [defaultModel];
        }

        for (let index = 0; index < this.members.length; index++) {
            const member = this.members[index];
            if(this.container.has(member)){
                let model = this.container.get(member);
                if(model.isActive) {
                    return [model];
                }
            }
        }

        return [];
    }

    /**
     * @inheritDoc
     */
    toData() {
        if(!this.members.length || !this.container.has(this.members[0])) {
            return [];
        }
        let node = thjis.getActive();
        if(!node.isActive) {
            return [];
        }

        return [{
            name: node.name,
            group: node.group,
            value: node.value,
        }];
    }
}