import { Service, Subject } from "../common";
import { Model } from "../nodes";

export class OutputService extends Service {

    groups;
    container;

    get name() {
        return 'output';
    }

    get all() {
        return Array.from(this.container.values);
    }

    /**
     * OutputService class constructor
     *
     * @param {Container} container
     * @param {Config} config
     */
    constructor(container, groups, config = null) {
        super(config);

        this.groups = groups;
        this.container = container;
	}

    findPlainActive() {
        let active = [];
        for (const [key, node] of this.container.entries) {
            if(!node.isActive || node.isDetached) {
                continue;
            }

            active.push(node);
        }

        return active;
    }

    findActive() {
        let active = [];
        let all = Array.from(this.container.values);
        for (let index = 0; index < all.length; index++) {
            let item = all[index];
            let name = item.group || item.name;
            if(!item.isActive || item.isDetached || null === name) {
                continue;
            }

            if(!active.hasOwnProperty(name)) {
                active[name] = [];
            }

            active[name].push(item)
        }

        return active;
    }

    getPlainOutput() {
        let output = [];
        let active = this.findPlainActive();

        active.forEach(node => {
            output.push({
                name: node.name,
                group: node.group,
                value: node.value,
            });
        });

        return output;
    }

    getOutput() {
        let output = [];
        let groups = this.groups.all;
        let all = Array.from(this.container.values);

        for (let index = 0; index < groups.length; index++) {
            const group = groups[index];

            output = output.concat(group.toData());
        }

        for (let index = 0; index < all.length; index++) {
            const element = all[index];
            if(null !== element.group || !element.isActive || element.isDetached) {
                continue;
            }

            output.push({
                name: element.name,
                group: null,
                value: element.value
            })
        }

        return output;
    }

    getOutputArray() {
        let output = [];
        let groups = this.groups.all;
        let all = Array.from(this.container.values);

        for (let index = 0; index < groups.length; index++) {
            const group = groups[index];

            output = output.concat(group.toData());
        }

        for (let index = 0; index < all.length; index++) {
            const element = all[index];
            if(null !== element.group || !element.isActive || element.isDetached) {
                continue;
            }

            output.push({
                name: element.name,
                value: element.value
            });
        }

        return output;
    }

    destroy() {
        if(null !== this.events) {
            this.off();
        }
        this.events = null;
        this.config = null;
        this.groups = null;
        this.container = null;

        return this;
    }
}