import { h, Component  as BaseComponent } from 'preact';
import { v4 as uuid } from "uuid";
import { DATASET_NAMESPACE } from "../constants";

export class Component extends BaseComponent {

    list;
    groups;
    items = [];

    constructor(props, state) {
        super(props, state);

        this.list = props.list;
        this.groups = props.groups;
        this.output = props.output;
        this.onUpdate = props.updateHandler;
        this.items = this.findActive();

        this.key = uuid();
        this.keyAttribute = `data-${DATASET_NAMESPACE}-html-view`;
        this.defaultAttributes = {
            wrapper: {
                data: `data-${DATASET_NAMESPACE}-html-wrapper`,
            },
            parent: {
                data: `data-${DATASET_NAMESPACE}-html-parent`,
            },
            children: {
                data: `data-${DATASET_NAMESPACE}-html-child`,
            },
            buttons: {
                data: `data-${DATASET_NAMESPACE}-html-buttons-wrapper`,
            },
        }

        this.prepareAttributes(this.props.attributes);
    }

    prepareAttributes(attributes) {
        for (let key in attributes) {
            if (attributes.hasOwnProperty(key)) {
                let part = attributes[key];
                if(part) {
                    for (const key in part) {
                        if (part.hasOwnProperty(key)) {
                            let attributeGroup = part[key];
                            if(Array.isArray(attributeGroup)) {
                                attributeGroup = attributeGroup.join(' ');
                            }
                        }
                    }

                    if('wrapper' === key) {
                        part[this.keyAttribute] = this.key;
                    }
                    if(this.defaultAttributes.hasOwnProperty(key)) {
                        part[this.defaultAttributes[key].data] = '';
                    }
                }
            }
        }
    }

    update() {
        this.items = this.findActive();
        this.setState();
    }

    clear() {
        let allItems = Array.from(this.list.values);
        for (let index = 0; index < allItems.length; index++) {
            let item = allItems[index];
            item.clear(false, { emittedFrom: 'html' });
        }

        this.items = [];
        this.setState();
    }

    reset() {
        let allItems = Array.from(this.list.values);
        for (let index = 0; index < allItems.length; index++) {
            let item = allItems[index];
            item.reset(false, { emittedFrom: 'html' });
        }

        this.update();
    }

    findActive() {
        let list = [];
        let active = this.output.findActive();
        let keys = Object.keys(active);

        for (let index = 0; index < keys.length; index++) {
            let key = keys[index];
            let nodes = active[key] || [];
            if(nodes.length > 1) {
                let group = this.groups.take(nodes[0].group);
                if(null !== group) {
                    nodes = group.getActive();
                }
            }

            list.push({
                key: uuid(),
                nodes: nodes
            });
        }

        return list;
    }

    removeHandler = key => {
        for (let i = 0; i < this.items.length; i++) {
            let item = this.items[i];
            if(item.key !== key) {
                continue;
            }

            let groupName = item.nodes[0].group;
            if(this.groups.has(groupName)) {
                this.groups.take(groupName).clear();
            } else {
                item.nodes.forEach(node => {
                    node.clear();
                });
            }

            break;
        }

        this.update();
        if(null !== this.onUpdate) {
            this.onUpdate();
        }
    }

    updateHandler = e => {
        this.update();
        if(null !== this.onUpdate) {
            this.onUpdate();
        }
    }

    clearHandler = e => {
        this.clear();
        if(null !== this.onUpdate) {
            this.onUpdate();
        }
    }

    resetHandler = e => {
        this.reset();

        let groups = this.groups.all;
        if(groups.length) {
            Promise.all(
                groups.map(g => g.synchronize())
            )
        }

        if(null !== this.onUpdate) {
            this.onUpdate();
        }
    }

	render(props, state) {
        return null;
	}
}