import { ListComponent, Component } from "../components";
import { Service } from "../common";
import { Container } from "../groups";
import { h, render } from 'preact';
import { DATASET_NAMESPACE, DEFAULTS } from "../constants";

/**
 * The HTML list service class
 *
 * @prop {VNode} node
 * @prop {Service} output
 * @prop {NodeList} htmlContainer
 */
export class HtmlService extends Service {

    node;
    /**
     * @type {Container}
     */
    groups;
    output;
    selector;
    component;
    htmlContainer;

    /**
     * Get the name of the service
     *
     * @returns {string}
     */
    get name() {
        return 'html';
    }

    /**
     * Get the container
     *
     * @returns {NodeList}
     */
    get container() {
        if(null !== this.htmlContainer) {
            return this.htmlContainer;
        }

        return document.querySelectorAll(this.selector);
    }

    /**
     * HtmlService class constructor
     *
     * @param {Container} groups
     * @param {string} selector
     * @param {any} config
     */
    constructor(groups, output, selector, config = null, updateHandler = null) {
        super(config);

        if(null === selector) {
            throw new TypeError("The container selector value is required for this HTML service");
        }

        let renderer = this.config.get('renderer', ListComponent);
        if(!(renderer.prototype instanceof Component || renderer === Component)) {
            throw new TypeError('Renderer must be instamce of StateBox.Component class');
        }

        this.renderer = renderer;
        this.groups = groups;
        this.output = output;
        this.selector = selector;
        this.updateHandler = updateHandler;
        this.htmlContainer = document.querySelectorAll(selector);
    }

    /**
     * Build HTML service
     *
     * @returns {this}
     */
    build() {
        const containers = this.container;
        const attributes = this.config.get('attributes', {wrapper: {}, parent: {}, children: {}, buttons: {}});
        if(containers.length) {
            this.node = h(this.renderer, {
                list: this.groups.list,
                output: this.output,
                groups: this.groups,
                attributes: attributes,
                updateHandler: this.updateHandler,
                buttons: this.config.get('buttons', []),
                enableButtons: this.config.get('controlButtons', false),
                ref: list => { this.component = list },
            });
            containers.forEach(node => {
                render(this.node, node);
            });
        }

        if(this.config.get('liveUpdate', false)) {
            if(typeof this.component !== 'undefined' && this.component) {
                let onChange = (node, state = {}) => {
                    if(state.hasOwnProperty('emittedFrom') && 'html' === state.emittedFrom) {
                        return;
                    }

                    this.component.update();
                }

                this.on('ready', () => this.component.update());
                this.on('destroy', () => this.component.update());
                this.on('change', onChange);
            }
        }

        return this;
    }

    /**
     * Destroy the HTML service
     *
     * @returns {this}
     */
    destroy() {
        let containers = this.container;
        if(containers.length) {
            let key = `[data-${DATASET_NAMESPACE}-html-view]`;
            containers.forEach(node => {
                node.querySelectorAll(key).forEach(i => {
                    if (i.parentNode !== null) {
                        i.parentNode.removeChild(i);
                    }
                })
            });
        }
        if(null !== this.events) {
            this.off();
        }

        this.node = null;
        this.events = null;
        this.output = null;
        this.selector = null;
        this.component = null;
        this.htmlContainer = null;

        return this;
    }
}