import { Model } from "../Model";
import { DATASET_NAMESPACE, DATASET_KEYS } from "../../constants";

export class Select extends Model {

    /**
     * @inheritDoc
     */
    static getType() {
        return 'select';
    }

    /**
	 * Get display value (if display value is not set returns value)
	 */
	get displayValue() {
        let node = this.node[0];
        let selected = node.selectedIndex;
        if(!this.data.optionsDisplayValues.hasOwnProperty(selected)) {
            this.parseOptionDisplayValue(node, selected);
        }

        return this.data.optionsDisplayValues[selected] || this.value;
	}

    /**
     * @inheritDoc
     */
    async parseAttributes(){
        await Model.prototype.parseAttributes.call(this);

        let node = this.node;
		let element = node[0];
        let dataset = element.dataset || {};

        this.data.name = node.prop('name') || this.data.name || null;

        let selected = -1;
        let options = node.find(`option[data-${DATASET_NAMESPACE}-default]`);
        if(options.length) {
            if(options.length > 1) {
                console.warn(`Too many default options selected! "data-${DATASET_NAMESPACE}-default" attribute can be used only one time!`);
            }

            let value = options.first().attr('value');
            if(typeof value !== 'undefined' && '' !== value) {
                console.error(`Cannot use "data-${DATASET_NAMESPACE}-default" attribute on the options with value!`);
            } else {
                if(typeof value === 'undefined') {
                    console.warn('At least empty value must be set for default option');

                    options.first().attr('value', '');
                }

                selected = options.first().index;
            }
        }

        this.data.displayValue = null;
        this.data.optionsDisplayValues = {};
        this.data.selectedIndex = selected;

        if(selected !== -1) {
            this.parseOptionDisplayValue(element, element.selectedIndex);
        } else {
            this.parseOptionDisplayValue(element, selected);
        }

        let value = this.parseValue();
        if(null !== value && '' !== value) {
            if(!dataset.hasOwnProperty(DATASET_KEYS.active)) {
                this.data.active = true;
            }
        }
    }

    parseOptionDisplayValue(element, index = null) {
        if(null === index) {
            throw new TypeError('Expected option index to be a number');
        }

        if(typeof element[index] === 'undefined') {
            return this;
        }

        let option = element[index];
        let displayValue = option.dataset[DATASET_KEYS.displayValue] || $(option).text() || null;
        this.data.optionsDisplayValues[index] = displayValue;

        return this;
    }

    /**
     * @inheritDoc
     */
    async attachDOMListners() {
        if(!this.node.length) {
            return;
        }

        let self = this;
        this.node.on('change', function(event) {
            let value = self.node.val();
            let state = null !== value && '' !== value;

            self.setValue(value);
            self.setState(state);
            self.synchronize();
            self.emit('update');
        })
    }

    /**
     * @inheritDoc
     */
    parseValue(){
        let node = this.node;
        let value = this.node.val() || null;

        return value;
    }

    /**
     * @inheritDoc
     */
    setNodeValue(value) {
        if(this.isImmutable) {
            return this;
        }

        let node = this.node;

        if(null === value) {
            node.val(null);
            node[0].selectedIndex = this.data.selectedIndex
        } else {
            if(Array.isArray(value)) {
                let selectableValues = [];
                value.forEach(val => {
                    if(node.find(`option[value="${val}"]`).length) {
                        selectableValues.push(val);
                    }
                });
                node.val(selectableValues);
            } else {
                if(!node.find(`option[value="${value}"]`).length) {
                    node.val(null);
                    node[0].selectedIndex = this.data.selectedIndex
                } else {
                    node.val(value);
                }
            }
        }

        return this;
    }

    /**
     * @inheritDoc
     */
    mutate(value, state, silent = false, ...args) {
        const emitionState = args[0] || {};
        silent = Boolean(silent);
        state = Boolean(state);

        this.setState(state, false, emitionState);
        this.setValue(value, emitionState);
        this.setNodeValue(value);

        if(!silent) {
            this.node.trigger('change');
        }

        return this;
    }
}