import { Model, Factory, Container as ModelContainer, Types as ModelTypes } from './nodes';
import { HtmlService, PersistenceService, OutputService } from "./services";
import { Container as GroupContainer, Types as GroupTypes } from "./groups";
import { Observer, Service, Config } from "./common";
import { uppercaseFirst } from "./helpers";
import { DEFAULTS, DATASET_NAMESPACE_CAMELIZED, DATASET_NAMESPACE, TICK } from "./constants";
import * as uuid from 'uuid';

export class Box extends Service {

    key;
    selector;
    wrapper = 'body';
    models = null;
	modelTypes = null;
	modelFactory = null;
    groups = null;
	groupTypes = null;
    watcher = null;
    output = null;
    html = null;

    get name() {
        return 'box';
    }

    get nodes() {
        return this.models.entries;
    }

    /**
     * DataBox constructor
     *
     * @param {string} selector
     * @param {string} container
     * @param {any} config
     */
    constructor(selector, container, customTypes = [], customGroups = [], config = {}) {
        super(config);
        if(!selector) {
            throw new TypeError("Selector is a required parameter")
        }

        this.key = uuid.v4().replace(/-/g, '');
        this.wrapper = container || 'body';
        this.selector = selector;
        this.config.merge({ selector, container, boxKey: this.key });
        this.modelTypes = new ModelTypes(customTypes);
        this.groupTypes = new GroupTypes(customGroups);
        this.models = new ModelContainer();
        this.groups = new GroupContainer(this.models, this.groupTypes);
        this.modelFactory = new Factory(this.config, this.modelTypes);
        this.output = new OutputService(
            this.models,
            this.groups,
            {}
        );

        if(this.config.get('html.enabled', true)) {
            let updateHandler = this.config.get('callbacks.onUpdate', null);
            if(null !== updateHandler) {
                updateHandler = updateHandler.bind(this);
            }
            this.html = new HtmlService(
                this.groups,
                this.output,
                this.config.get('html.container'),
                this.config.get('html'),
                updateHandler
            );
        }

        this.attachWatcher();
    }

    async register(nodes) {
        let bind = (nodes) => {
            this.models.bind(nodes);
            this.watch(nodes);
            [this.output, this.html].forEach(service => {
                if(null !== service) {
                    service.watch(nodes);
                }
            });
        };

        if(!Array.isArray(nodes)) {
            if(nodes instanceof Model) {
                bind(nodes);
            } else {
                let descriptor = await this.modelFactory.make(nodes, this.config)
                bind(descriptor);
            }
        } else {
            let descriptors = await this.modelFactory.makeBatch(nodes, this.config);
            bind(descriptors);
		}

        return this;
    }

    async build() {
        await Promise.all([
            Array.from(this.models.values).map(async node =>  {
                await node.build();
            })
        ]);

        if(null !== this.html) {
            await this.html.build();
        }

        return this;
    }

    clear() {
        [this.output, this.html].forEach(service => {
            if(null === service) {
                return;
            }

            service.off();
            service.registerEvents();
        });
        this.models.clear();

        return this;
    }

    destroy() {
        if(null !== this.models) {
            this.models.clear();
        }
        [this.output, this.html].forEach(service => {
            if(null === service) {
                return;
            }

            service.destroy();
        });
        if(null !== this.watcher) {
            if(this.canWatchMutations()) {
                this.watcher.disconnect();
            } else {
                clearInterval(this.watcher);
            }
        }
        if(null !== this.events) {
            this.off();
        }

        this.events = null;
        this.modelTypes = null;
        this.groupTypes = null;
        this.config = null;
        this.wrapper = null;
        this.watcher = null;
        this.modelFactory = null;
        this.selector = null;
        this.html = null;
        this.output = null;
        this.models = null;

        return this;
    }

    attachWatcher() {
        if(null !== this.watcher) {
            return this;
        }

        let self = this;
        let key = uppercaseFirst(this.key);
        if(!isNaN(key[0])) {
            key = `-${key}`;
        }

        let boxKey = `${DATASET_NAMESPACE_CAMELIZED}${key}Id`;
        let boxAttribute = `data-${DATASET_NAMESPACE}-${this.key}-id`;
        let canRemoveOrphans = this.config.get('removeOrphans', true);

        if(!this.canWatchMutations()) {
            // Fallback method to handle deletion
            this.watcher = setInterval(() => {
                let inserted = document.querySelectorAll(`${this.selector}:not([${boxAttribute}])`);
                if(inserted.length) {
                    inserted.forEach(async node => {
                        let model;
                        try {
                            model = await self.modelFactory.make($(node));
                            await self.register(model);
                            await model.build();
                        } catch (error) {
                            if(model) {
                                model.destroy()
                            };

                            console.error(error);
                        }
                    });
                }
                if(canRemoveOrphans) {
                    this.models.removeOrphans();
                }
            }, TICK);

            return this;
        }

        let handleRemoval = function(record) {
            let deletedNodes = [];
            record.removedNodes.forEach(removedNode => {
                if(!(removedNode instanceof Element)) {
                    return;
                }

                if(removedNode.dataset) {
                    let id = removedNode.dataset[boxKey];
                    if(id) {
                        deletedNodes.push(id);

                        return;
                    }
                }

                let deletions = removedNode.querySelectorAll(`[${boxAttribute}]`);
                for (let i = 0; i < deletions.length; i++) {
                    let node = deletions[i];
                    let id = node.dataset[boxKey] || null;
                    if(null !== id) {
                        deletedNodes.push(id);
                    }
                }
            });

            for (let i = 0; i < deletedNodes.length; i++) {
                let id = deletedNodes[i];
                if(self.models.has(id)) {
                    self.models.get(id).destroy();
                }
            }
        };

        let handleInsert = function(record) {
            let insertedNodes = [];
            record.addedNodes.forEach((/** @type {Element} */ addedNode) => {
                if(!(addedNode instanceof Element)) {
                    return;
                }

                if(addedNode.matches(self.selector)) {
                    insertedNodes.push(addedNode)

                    return;
                }

                let inserts = addedNode.querySelectorAll(self.selector);
                for (let i = 0; i < inserts.length; i++) {
                    insertedNodes.push(inserts[i]);
                }
            });

            insertedNodes.forEach(async node => {
                let id = node.dataset[boxKey];
                if(id) {
                    if(self.models.has(id)) {
                        return;
                    }
                }

                let model;
                try {
                    model = await self.modelFactory.make($(node));
                    await self.register(model);
                    await model.build();
                } catch (error) {
                    if(model) {
                        model.destroy()
                    };

                    console.error(error);
                }
            })
        };

        this.watcher = new MutationObserver(function (records) {
            records.forEach(record => {
                if(record.removedNodes.length) {
                    handleRemoval(record);
                }
                if(canRemoveOrphans && record.addedNodes.length) {
                    handleInsert(record);
                }
            });
        });

        let wrappers = document.querySelectorAll(this.wrapper);
        for (let index = 0; index < wrappers.length; index++) {
            this.watcher.observe(wrappers[index], { childList: true, subtree: true });
        }
    }

    canWatchMutations() {
        return "MutationObserver" in window;
    }

    /**
     * Registers a default set of events
     */
    registerEvents() {
        let callbacks = this.config.get('callbacks', DEFAULTS.callbacks);
        let updateCallback = callbacks.onUpdate || null;

        this.on('destroy', (node) => {
            /**
             * @type {Model} node
             */
            this.models.unbind(node);
        });
        this.on('ready', node => this.groups.resolveGroup(node));

        for (const key in callbacks) {
            if (callbacks.hasOwnProperty(key) && ['onReset', 'onClear', 'onReady', 'onChange'].indexOf(key) !== -1) {
                const callback = callbacks[key];
                let name = key.charAt(2).toLowerCase() + key.slice(3);
                if(!name || null === callback) {
                    continue;
                }

                this.on(name, (node, ...args) => callback.call(this, node, ...args));
            }
        }

        if(null !== updateCallback) {
            this.on('update', (node, state = {}, ...args) => {
                if(state.hasOwnProperty('emittedFrom')) {
                    let disallowed =  ['html', 'group'];
                    if(disallowed.indexOf(state.emittedFrom) !== -1) {
                        return;
                    }
                }

                updateCallback.call(this, state, ...args);
            });
        }
    }
}