import { Button } from "./Button";
import { DATASET_KEYS } from "../../constants";

export class InputButton extends Button {

    /**
     * @inheritDoc
     */
    static get type() {
        return 'inputButton';
    }

    /**
	 * Set value to the node
	 *
	 * @param {any} value
	 * @public
	 * @returns {this}
	 */
	setNodeValue(value) {
        if(this.isImmutable) {
            return this;
        }

        this.node.val(value);

        return this;
    }

    /**
     * @inheritDoc
     */
    async parseAttributes(){
        await Button.prototype.parseAttributes.call(this);

        let node = this.node;
		let element = node[0];
        let dataset = element.dataset || {};

        this.data.name = this.node.prop('name') || this.data.name || null;

        let value = this.parseValue();
        if(null !== value && '' !== value) {
            if(!dataset.hasOwnProperty(DATASET_KEYS.active)) {
                this.data.active = true;
            }
        }
    }

    /**
     * @inheritDoc
     */
    parseValue(){
        let node = this.node;
        let value = this.node.val() || null;
        if(null === value) {
            value = node[0].dataset[DATASET_KEYS.value] || null;
        }

        return value;
    }
}