import { Subject } from "../common/Subject";
import { attributeToBoolean } from "../helpers";
import { v4 as uuid } from "uuid";
import { DATASET_KEYS, DATASET_NAMESPACE, CLEARED_ATTRIBUTES } from "../constants";

export class Model extends Subject {

	id;
    key = '';
	data = {};
	isDeleted = false;
	isInitialized = false;
	useSecureInit = true;
    hasCachedNode = false;
    supressEvents = false;
    cachedNode = null;

	/**
	 * Node model constructor
	 *
	 * @param {JQuery.Node} node
	 */
	constructor(node, {
		secure = true,
        clearShared = true,
        box = '',
	}) {
        super();
		if (node.length === 0) {
			this.destroy();
        }

        this.id = uuid();
        this.key = `data-${DATASET_NAMESPACE}-${box}-id`;
		this.useSecureInit = secure;
		this.setCachedNode(node);
	}

	/**
	 * Get the type of the node model
	 *
	 * @returns {string}
	 */
	static get type() {
		return 'default';
	}

	/**
	 * Get the node
	 *
	 * @returns {JQuery.Node}
	 */
	get node() {
		if (this.hasCachedNode) {
			return this.cachedNode;
        }

		return $(`[${this.key}="${this.id}"]`);
	}

	/**
	 * Get the name od the node
	 *
	 * @returns {string|null}
	 */
	get name() {
		return this.data.name || null;
	}

	/**
	 * Get the name of the group
	 *
	 * @returns {string|null}
	 */
	get group() {
		return this.data.group || null;
    }

    /**
	 * Get the type of the group
	 *
	 * @returns {string|null}
	 */
    get groupType() {
        return this.data.groupType || null;
    }

	/**
	 * Get display name (if display name is not set returns name)
	 *
	 * @returns {string|null}
	 */
	get displayName() {
		return this.data.displayName || this.name || null;
	}

	/**
	 * Set display name (if display name is not set returns name)
	 *
	 * @param {string|null}
	 */
	set displayName(name) {
		this.data.displayName = name;
	}

	/**
	 * Get display value (if display value is not set returns value)
	 */
	get displayValue() {
		return this.data.displayValue || this.value;
	}

	/**
	 * Get display description (if is set)
	 *
	 * @returns {string|null}
	 */
	get displayDescription() {
		return this.data.displayDescription || null;
	}

	/**
	 * Set display description
	 *
	 * @param {string|null}
	 */
	set displayDescription(description) {
		this.data.displayDescription = description;
	}

	/**
	 * Get the value
	 *
	 * @returns {any|null}
	 */
	get value() {
		return this.data.value ? this.data.value.current || null : null;
    }

    /**
     * Get current state
     */
    get state() {
        return this.data.active;
    }

	/**
	 * Get the base (initial) value
	 *
	 * @return {any|null}
	 */
	get baseValue() {
		return this.data.value.base || null;
	}

	/**
	 * Get the base state
	 *
	 * @returns {boolean}
	 */
	get baseState() {
		return this.data.baseState;
	}

	/**
	 * Flag that indicates if model is ready
	 *
	 * @returns {boolean}
	 */
	get isReady() {
		return this.isInitialized;
	}

	/**
	 * Flag that indicates if model is active
	 *
	 * @returns {boolean}
	 */
	get isActive() {
		return this.data.active;
	}

	/**
	 * Flag that indicates if model is detached to the set
	 *
	 * @returns {boolean}
	 */
	get isDetached() {
		return this.data.detached;
    }

    /**
	 * Flag that indicates if model has immutable value
	 *
	 * @returns {boolean}
	 */
    get isImmutable() {
        return this.data.immutable;
    }

    /**
	 * Flag that indicates if model is default for group
	 *
	 * @returns {boolean}
	 */
    get isDefault() {
        return this.data.default;
    }

    /**
     * Flag that indicates if node still exists in DOM
     */
    get isExistsInDOM() {
        return document.querySelectorAll(`[${this.key}="${this.id}"]`).length > 0;
    }

    /**
     * Flag that indicates if model contains virtuals value (not present in DOM)
    */
    get isVitual() {
        return true;
    }

	/**
	 * Build the model
	 *
	 * @public
	 * @return {Promise<this>}
	 */
	async build() {
		await Promise.all([
			this.parseNode(),
			this.attachIdentifier(),
			this.attachDOMListners(),
		]);

		if (null === this.name) {
			throw new TypeError("Initialization failed  - node name is required")
		}

		await this.clearAttributes();
		this.clearCachedNode();
        this.emit('ready');

		return this;
	}

	/**
	 * Set model value
	 *
	 * @param {any} value
	 * @public
	 * @returns {this}
	 */
	setValue(value, ...args) {
        if(this.isImmutable) {
            return this;
        }

		let old = this.data.value.current;
		this.data.value.current = value;
		if (old !== this.data.value.current) {
			this.emit('change', ...args);
		}

		return this;
	}

	/**
	 * Set value to the node
	 *
	 * @param {any} value
	 * @public
	 * @returns {this}
	 */
	setNodeValue(value) {
        if(this.isImmutable) {
            return this;
        }

        this.node.val(value);

        return this;
    }

	/**
	 * Destroy state tracker
	 *
     * @param {Array<any>} args
	 * @public
	 * @return {this}
	 */
	destroy(...args) {
		this.emit('destroy', ...args);
		this.unsubscribeAll();
		this.node.off();

		return this;
	}

	/**
	 * Activate node
	 *
	 * @param {boolean} silent
     * @param {Array<any>} args
	 * @public
	 * @returns {this}
	 */
	activate(silent = false, ...args) {
        if(this.isActive) {
            return;
        }

		let old = this.data.active;
		this.data.active = true;
		if (!silent && !this.silent) {
            let state = args.shift() || {};
            state.step = 'activate';
            this.emit('activated', state, ...args);
            if(null !== this.group) {
                if(!state.sync) {
                    this.emit('activated.all', state, ...args);
                }
            }

			if (old !== this.data.active) {
				this.emit('change', state, ...args);
            }
        }

		return this;
	}

	/**
	 * Deactive node
	 *
	 * @param {boolean} silent
     * @param {Array<any>} args
	 * @public
	 * @returns {this}
	 */
	deactivate(silent = false, ...args) {
        if(!this.isActive) {
            return;
        }

		let old = this.data.active;
		this.data.active = false;
		if (!silent && !this.silent) {
            let state = args.shift() || {};
            state.step = 'deactivate';
            this.emit('deactivated', state, ...args);
            if(null !== this.group) {
                if(!state.sync) {
                    this.emit('deactivated.all', state, ...args);
                }
            }

			if (old !== this.data.active) {
				this.emit('change', state, ...args);
            }
        }

		return this;
	}

	/**
	 * Reset the node state
     *
	 * @param {boolean} silent
     * @param {Array<any>} args
	 * @public
	 * @returns {this}
	 */
	reset(silent = false, ...args) {
        let originalSilentState = this.silent;

        this.mute();
        this.setState(this.baseState);
		this.setValue(this.baseValue);
        this.setNodeValue(this.baseValue);
        this.unmute();

        if(originalSilentState) {
            this.mute()
        }

        if (!silent && !this.silent) {
            let state = args[0]|| {};
            state.step = 'reset';
            this.emit('reset', ...args);
            this.emit('change', ...args);
        }

		return this;
	}

	/**
	 * Clear the node state

	 * @param {boolean} silent
	 * @param {any} state
     * @param {Array<any>} args
	 * @public
	 * @returns {this}
	 */
	clear(silent = false, ...args) {
        let originalSilentState = this.silent;

        this.mute();
        this.setState(false);
		this.setValue(null);
        this.setNodeValue(null);
        this.unmute();

        if(originalSilentState) {
            this.mute()
        }

        if (!silent && !this.silent) {
            let state = args[0] || {};
            state.step = 'clear';
            this.emit('clear', ...args);
            this.emit('change', ...args);
        }

		return this;
	}

	/**
	 * Detach this model from set
	 *
     * @param {Array<any>} args
	 * @public
	 * @returns {this}
	 */
	detach(...args) {
		this.data.detached = true;
		this.emit('detached', ...args);

		return this;
	}

	/**
	 * Attach this model to set
	 *
     * @param {Array<any>} args
	 * @public
	 * @return {this}
	 */
	attach(...args) {
		this.data.detached = false;
		this.emit('attached', ...args);

		return this;
	}

	/**
	 * Propagate current node state
	 *
     * @param {Array<any>} args
	 * @public
	 * @returns {this}
	 */
	synchronize(...args) {
		this.emit('synchronize', ...args);

		return this;
	}

	/**
	 * Set the state of the node/model
	 *
	 * @param {boolean} state
	 * @param {boolean} silent
     * @param {Array<any>} args
	 * @public
	 * @returns {this}
	 */
	setState(state, silent = false, ...args) {
        if (Boolean(state)) {
			this.activate(silent, ...args);
		} else {
			this.deactivate(silent, ...args);
		}

		return this;
	}

	/**
	 * Toggle the state of the model
	 *
	 * @param {boolean} silent
     * @param {Array<any>} args
	 * @public
	 * @returns {this}
	 */
	toggleState(silent = false, ...args) {
		if (this.isActive) {
			this.deactivate(silent, ...args);
		} else {
			this.activate(silent, ...args);
		}

		return this;
	}

	/**
	 * Mutate the node conform the state
	 *
	 * @param {boolean} value
	 * @param {boolean} state
	 * @param {boolean} silent
	 * @public
	 * @returns {this}
	 */
	mutate(value, state, silent = false, ...args) {
        silent = Boolean(silent);
        state = Boolean(state);

        let emitionState = args[0] || {};

        this.setState(state, false, emitionState);
        this.setValue(value, emitionState);
        this.setNodeValue(value);

        return this;
    }

	/**
	 * Parse node to collect its state
	 *
	 * @protected
	 */
	async parseNode() {
		this.data.baseState = false;

		await this.parseAttributes();

        this.emit('readState');
		this.data.value = {};
		this.data.value.current = this.parseValue();
		if (!this.isInitialized) {
			this.data.value.base = this.parseBaseValue();
			this.data.baseState = this.data.active;
			this.isInitialized = true;

			this.emit('initialized');
		}
	}

	/**
	 * Parse node attributes
	 *
	 * @protected
	 */
	async parseAttributes() {
		let node = this.node;
		let element = node[0];
        let dataset = element.dataset || {};

		// Determine if filter is activated by checking the state of the active flag (with fallback)
		this.data.active = false;
		if (dataset.hasOwnProperty(DATASET_KEYS.active)) {
            this.data.active = attributeToBoolean(dataset[DATASET_KEYS.active])
		}

        // Determines type
        this.data.type = dataset[DATASET_KEYS.type] || null;

		// Determine if filter is detached
		this.data.detached = false;
		if (dataset.hasOwnProperty(DATASET_KEYS.detached)) {
            this.data.detached = attributeToBoolean(dataset[DATASET_KEYS.detached])
		}

		// Determine if filter is activated by checking the state of the active flag (with fallback)
		this.data.default = false;
		if (dataset.hasOwnProperty(DATASET_KEYS.default)) {
            this.data.default = attributeToBoolean(dataset[DATASET_KEYS.default])
		}

        // Determine the immatubility of the value
        this.data.immutable = false;
		if (dataset.hasOwnProperty(DATASET_KEYS.immutable)) {
            this.data.immutable = attributeToBoolean(dataset[DATASET_KEYS.immutable])
		}

		// Determine the name of the node
		this.data.name = dataset[DATASET_KEYS.name] || null;
		// Determine the group of the node. if it is not set we will take the name of the node as group by default
		this.data.group = dataset[DATASET_KEYS.group] || null;
		this.data.groupType = dataset[DATASET_KEYS.groupType] || null;
		// Determine the display name for this node state. If expected key is not set then use fallback key
		this.data.displayName = dataset[DATASET_KEYS.displayName] || null;
		// Determine the display value of the node. If expected key is not set then use fallback key
		this.data.displayValue = dataset[DATASET_KEYS.displayValue] || null;
		// Determine the description value of the node
        this.data.displayDescription = dataset[DATASET_KEYS.displayDescription] || null;
	}

	/**
	 * Attach a identifier to node (if is not set)
	 *
	 * @protected
	 */
	async attachIdentifier() {
		if (!this.isInitialized) {
			this.node.attr(this.key, this.id);
		}
	}

	/**
	 * Attach DOM listners
	 *
	 * @protected
	 */
	async attachDOMListners() {
		if (!this.node.length) {
			return;
		}

		let self = this;
		this.node.on('click', function (event) {
			event.preventDefault();
			self.toggleState();
            self.synchronize();
            self.emit('update');
		});
	}

	/**
	 * Parse value from the node
	 *
	 * @protected
	 * @returns {any|null}
	 */
	parseValue() {
		let node = this.node;
		let value = node[0].dataset[DATASET_KEYS.value] || null;

		return value;
	}

	/**
	 * Get the value from the node or prse it if not set
	 *
	 * @protected
	 * @returns {any|null}
	 */
	parseBaseValue() {
		if (this.data.baseValue) {
			return this.data.baseValue;
		}

		return this.parseValue();
    }

	/**
	 * Recalculate node value
	 *
	 * @protected
	 */
	recalculateValue() {
		this.data.value.current = this.parseValue();

		return this;
	}

	/**
	 * Clean sensetive attributes
	 *
	 * @protected
	 */
	async clearAttributes() {
		if (!this.useSecureInit) {
			return this;
		}

		const node = this.node;
		const clearQueue = CLEARED_ATTRIBUTES;
		for (let index = 0; index < clearQueue.length; index++) {
			node.attr(clearQueue[index], null);
		}
	}

	/**
	 * Set node cache
	 *
	 * @param {JQuery.Node} node
	 * @protected
	 */
	setCachedNode(node) {
		if (!this.isInitialized) {
			this.cachedNode = node;
			this.hasCachedNode = true;
		}
	}

	/**
	 * Clear node cache
	 *
	 * @protected
	 */
	clearCachedNode() {
		this.cachedNode = null;
		this.hasCachedNode = false;
	}
}