import { Group } from "../Group";
import { Model } from "../../nodes";

export class Unique extends Group {

    constructor(name) {
        super(name);

        this.on('synchronize', node => { node.isActive && this.onPersist(node, false, [node]) });
        this.on('activated.all', node => this.onActivate(node, true, [node]));
    }

    /**
     * Persist handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onPersist(emitter, silent = false, skip = []) {
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            let node = this.container.get(id);
            if(node === emitter || skip.indexOf(node) !== -1 || skip.indexOf(id) !== -1 ) {
                return;
            }

            node.mutate(null, false, true, { emittedFrom: 'group' });
        });
    }

    /**
     * Activate handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onActivate(emitter, silent = false, skip = []) {
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            let node = this.container.get(id);
            if(node === emitter || skip.indexOf(node) !== -1 || skip.indexOf(id) !== -1 || !node.isActive) {
                return;
            }

            node.deactivate(silent, { emittedFrom: 'group', sync: true });
        });
    }

    /**
     * @inheritDoc
     */
    getActive() {
        for (let index = 0; index < this.members.length; index++) {
            const member = this.members[index];
            if(this.container.has(member)){
                let model = this.container.get(member);
                if(model.isActive) {
                    return [model];
                }
            }
        }

        return [];
    }

    toData() {
        for (let index = 0; index < this.members.length; index++) {
            if(!this.container.has(this.members[index])) {
                continue;
            }

            let node = this.container.get(this.members[index]);
            if(!node.isActive) {
                continue;
            }

            return [{
                name: node.name,
                group: node.group,
                value: node.value,
            }]
        }

        return [];
    }
}