import { Container } from "../nodes";
import { Observer } from "../common";
import { Model } from "../nodes";

/**
 * An abstract group of nodes
 */
export class Group  extends Observer {

    /**
     * List of group members
     */
    members = [];

    /**
     * Name of the group
     */
    name;

    /**
     * Total allowed amount of mmembers
     */
    limit = null;

    /**
     * Container with nodes
     *
     * @returns {Container}
     */
    get container() {
        if(typeof this.models === null) {
            return new Container();
        }

        return this.models;
    }

    /**
     * Group constructor
     *
     * @param {string} name
     * @param {number|null} limit
     */
    constructor(name) {
        super();
        if(typeof name !== 'string') {
            throw new TypeError("Name must be of type string");
        }

        this.name = name;
        this.on('destroy', node => this.drop(node.id));
    }

    /**
     * Empty and fill goup with items
     *
     * @param {Array<Model>} members
     */
    fill(members = []) {
        this.members = [];
        this.fill(members);

        return this;
    }

    /**
     * Merge arrray of models into current group
     *
     * @param {Array<Model>} members
     */
    merge(members = []) {
        members.forEach(member => this.add(member));

        return this;
    }

    /**
     * Add a member to the group
     *
     * @param {Model|string} member
     * @returns {this}
     */
    add(member) {
        if(!(member instanceof Model)) {
            throw new TypeError("Invalid group member - expected to be node model");
        }

        if(this.limit !== null && this.members.length >= this.limit) {
            throw new Error("Cannot add more elements to group - limit is reached");
        }

        this.members.push(member.subscribe(this).id);
        this.synchronize();

        return this;
    }

    /**
     * Add the group member by its ID
     *
     * @returns {Model|null}
     */
    take(member) {
        if(typeof member !== 'string') {
            throw new TypeError("Invalid group member ID - expected string");
        }

        if(this.members.indexOf(member) !== -1 && this.container.has(member)) {
            return this.container.get(member);
        }

        return null;
    }

    /**
     * Remove member from the group by its ID
     *
     * @param {string} member
     */
    drop(member) {
        if(!(member instanceof Model) && typeof member !== 'string') {
            throw new TypeError("Invalid group member - expected to be node model or string");
        }

        let id = typeof member === 'string' ? member : member.id;
        let index = this.members.indexOf(id);
        if(index !== -1) {
            if(this.container.has(this.members[index])) {
                this.container.get(this.members[index]).unsubscribe(this);
            }
            this.members.splice(index, 1);
        }

        return this;
    }

    /**
     * Check if group has this member by its ID
     *
     * @param {string} member
     */
    has(member) {
        if(typeof member !== 'string') {
            return false;
        }

        if(this.members.indexOf(member) !== -1 && this.container.has(member)) {
            return true;
        }

        return null;
    }

    /**
     * Check if group contains this member by its ID
     *
     * @param {Model} member
     */
    contains(member) {
        if(!(member instanceof Model) ) {
            return false;
        }

        let id = member.id;
        if(this.members.indexOf(id) !== -1 && this.container.has(id)) {
            return true;
        }

        return null;
    }

    /**
     * Persist handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onPersist(emitter, silent = false, skip = []) {}

    /**
     * Deactivate handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onDeactivate(emitter, silent = false, skip = []) {}

    /**
     * Activate handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onActivate(emitter, silent = false, skip = []) {}

    /**
     * Reset handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onReset(emitter, silent = false, skip = []) {}

    /**
     * Clear handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onClear(emitter, silent = false, skip = []) {}

    /**
     * Deactivate ggroup nodes
     *
     * @param {boolean} silent
     */
    deactivate(silent = false) {
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            this.container.get(id).deactivate(silent, { emittedFrom: 'group' });
        });
    }

    /**
     * Activate group nodes
     *
     * @param {boolean} silent
     */
    activate(silent = false) {
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            this.container.get(id).activate(silent, { emittedFrom: 'group' });
        });
    }

    clear(silent = false) {
        for (let index = 0; index < this.members.length; index++) {
            const member = this.members[index];
            if(this.container.has(member)) {
                this.container.get(member).clear(silent, { emittedFrom: 'group' });
            }
        }

        return this;
    }

    /**
     * Reset group nodes
     *
     * @param {Model} emitter
     * @param {any} value
     * @param {boolean} state
     * @param {Array<Model>} skip
     */
    reset(silent = false) {
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            this.container.get(id).reset(silent, { emittedFrom: 'group' });
        });
    }

    async synchronize() {
        let member = this.getDefault();
        if(null === member) {
            member = this.getActive().pop();
        }

        if(member && member.isActive) {
            member.synchronize();
        }

        return this;
    }

    /**
     * Get deafult group member
     *
     * @returns {Model}
     */
    getDefault() {
        for (let index = 0; index < this.members.length; index++) {
            const member = this.members[index];
            if(this.container.has(member)){
                let model = this.container.get(member);
                if(model.isDefault) {
                    return model;
                }
            }
        }

        return null;
    }

    /**
     * Get active models in group
     *
     * @returns {Array<Model>}
     */
    getActive() {
        let active = [];
        for (let index = 0; index < this.members.length; index++) {
            const member = this.members[index];
            if(this.container.has(member)){
                let model = this.container.get(member);
                if(model.isActive) {
                    active.push(model);
                }
            }
        }

        return active;
    }

    /**
     * Returns array of gropu members
     */
    toArray(){
        return this.members.map(member => {
            return this.container.has(member) ? this.container.get(member) : null;
        }).filter(member => {
            return null !== member;
        });
    }

    /**
     * Returns group members data
     */
    toData() {
        let data = [];
        for (let index = 0; index < this.members.length; index++) {
            if(!this.container.has(this.members[index])) {
                continue;
            }

            let node = this.container.get(this.members[index]);
            if(!node.isActive) {
                continue;
            }

            data.push({
                name: node.name,
                group: node.group,
                value: node.value,
            });
        }

        return data;
    }
}