import { Button, Component, ListItem } from ".";

export class ListComponent extends Component {

	render({ enableButtons, buttons, attributes}) {
        let items = this.items;

        return (
            <div {...attributes.wrapper}>
                <ul {...attributes.parent}>
                    {
                        items.length
                            ? items.map( item => (
                                <ListItem id={item.key}
                                    list={item.nodes}
                                    onDestroy={this.removeHandler}
                                    attributes={attributes.children}/>
                                )
                            )
                            : null
                    }
                </ul>
                <div {...attributes.buttons}>
                    {
                        enableButtons
                            ? buttons.map(button => (
                                <Button id={button.id}
                                    text={button.text}
                                    type={button.type}
                                    attributes={button.attributes || {}}
                                    onClick={button.callback}
                                    onUpdate={this.updateHandler}
                                    onReset={this.resetHandler}
                                    onClear={this.clearHandler}/>
                                )
                            )
                            : null
                    }
                </div>
            </div>
		);
	}
}