import { Model } from "../Model";
import { attributeToBoolean } from "../../helpers";
import { DATASET_KEYS } from "../../constants";

export class Button extends Model {

    /**
     * @inheritDoc
     */
    static get type() {
        return 'button';
    }

    /**
     * @inheritDoc
     */
    parseAttributes() {
        Model.prototype.parseAttributes.call(this);

        let node = this.node;
		let element = node[0];
        let dataset = element.dataset || {};

        // By default value in the buttons is immutable
        this.data.immutable = true;
		if (dataset.hasOwnProperty(DATASET_KEYS.immutable)) {
            this.data.immutable = attributeToBoolean(dataset[DATASET_KEYS.immutable])
		}
    }

    /**
     * @inheritDoc
     */
    async attachDOMListners() {
        if(!this.node.length) {
            return;
        }

        let self = this;
        this.node.on('click', function(event) {
            const oldState = self.isActive;

            self.setState(true);
            self.synchronize();
            if(self.isActive !== oldState) {
                self.emit('update');
            }
        });
    }
}