import { Input } from "./Input";
import { DATASET_KEYS } from "../../constants";
import { attributeToBoolean } from "../../helpers";

export class InputRadio extends Input {

    /**
     * @inheritDoc
     */
    static get type() {
        return 'inputRadio';
    }

    /**
     * @inheritDoc
     */
    async parseAttributes(){
        await Input.prototype.parseAttributes.call(this);

        let checkedAttribute = this.node.attr('checked');
        let dataset = this.node[0].dataset;

        this.data.active = false;
        if(
            typeof checkedAttribute !== 'undefined' &&
            !dataset.hasOwnProperty(DATASET_KEYS.active)
        ) {
            this.data.active = this.node.prop('checked');
        } else {
            if (dataset.hasOwnProperty(DATASET_KEYS.active)) {
                this.data.active = attributeToBoolean(dataset[DATASET_KEYS.active])
            }
        }

        // By default value is immutable
        this.data.immutable = true;
		if (dataset.hasOwnProperty(DATASET_KEYS.immutable)) {
            this.data.immutable = attributeToBoolean(dataset[DATASET_KEYS.immutable])
		}
    }

    /**
     * @inheritDoc
     */
    async attachDOMListners() {
        if(!this.node.length) {
            return;
        }

        let self = this;
        this.node.on('unchecked', function(event) {
            self.deactivate();
        });
        this.node.on('change', function(event) {
            if(!self.isActive) {
                self.activate();
                self.synchronize();
                self.emit('update');
            }

            $(`input[type="radio"][name="${self.name}"]:not([${self.key}="${self.id}"])`).trigger('unchecked');
        });
    }

    /**
     * @inheritDoc
     */
    setNodeValue(value) {
        this.node.prop('checked', Boolean(value));

        return this;
    }

    /**
	 * Reset the node state
     *
	 * @param {boolean} silent
     * @param {Array<any>} args
	 * @public
	 * @returns {this}
	 */
	reset(silent = false, ...args) {
        let originalSilentState = this.silent;

        this.mute();
        this.setState(this.baseState);
		this.setValue(this.baseValue);
        this.setNodeValue(this.baseState);
        this.unmute();

        if(originalSilentState) {
            this.mute()
        }

        if (!silent && !this.silent) {
            let state = args[0]|| {};
            state.step = 'reset';
            this.emit('reset', ...args);
            this.emit('change', ...args);
        }

		return this;
	}

    /**
     * @inheritDoc
     */
    mutate(value, state, silent = false) {
        const emitionState = args[0] || {};
        silent = Boolean(silent);
        state = Boolean(state);

        this.setState(state, false, emitionState);
        this.setValue(value, emitionState);
        this.setNodeValue(this.isActive);
        if(!silent) {
            node.trigger('change');
        }

        return this;
    }
}