import { Group } from "../Group";
import { Model } from "../../nodes";

export class Pivot extends Group {

    value = null;
    activeMember = null;
    defaultMember = null;

    constructor(name) {
        super(name);

        this.on('synchronize', node => this.onPersist(node, true));
        this.on('deactivated', (node, state = {}) => {
            if(state.hasOwnProperty('emittedFrom') && 'group' === state.emittedFrom) {
                return;
            }

            this.onDeactivate(node, true);
        });
        this.on('activated', (node, state = {}) => {
            if(state.hasOwnProperty('emittedFrom') && 'group' === state.emittedFrom) {
                return;
            }

            this.onActivate(node, true);
        });
        this.on('destroy', node => this.onDestroy(node, true));
        this.on('reset', (node, state = {}) => {
            if(state.hasOwnProperty('emittedFrom') && 'group' === state.emittedFrom) {
                return;
            }

            this.onReset(node, false, [node]);
            this.synchronize();
        });
        this.on('clear', (node, state = {}) => {
            if(state.hasOwnProperty('emittedFrom') && 'group' === state.emittedFrom) {
                return;
            }

            this.onClear(node, false, [node]);
        });
    }

    /**
     * Add a member to the group
     *
     * @param {Model|string} member
     * @returns {this}
     */
    add(member) {
        if(!(member instanceof Model)) {
            throw new TypeError("Invalid group member - expected to be node model");
        }

        if(this.limit !== null && this.members.length >= this.limit) {
            throw new Error("Cannot add more elements to group - limit is reached");
        }

        this.members.push(member.subscribe(this).id);
        if(member.isActive) {
            this.activeMember = member.id;
        }
        if(member.isDefault) {
            this.defaultMember = member.id;
        }

        this.synchronize();

        return this;
    }

    async synchronize() {
        if(!this.members.length) {
            return;
        }

        let member = this.getActive();
        if(member.length) {
            member = member.pop();
        } else {
            member =this.getDefault();
        }
        let value = member ? member.value : null;

        this.value = value;
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }
            let node = this.container.get(id);

            node.mutate(this.value, node.isActive, true, { emittedFrom: 'group' });
        });

        return this;
    }

    /**
     * Persist handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onPersist(emitter, silent = false, skip = []) {
        if(emitter.isActive) {
            this.activeMember = emitter.id;
            if(emitter.value !== this.value) {
                this.value = emitter.value;
            }
        }

        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            let node = this.container.get(id);
            if(skip.indexOf(node) !== -1 || skip.indexOf(id) !== -1) {
                return;
            }

            (node !== emitter) && node.mutate(this.value, false, true, { emittedFrom: 'group' });
        });
    }

    /**
     * Activate handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onActivate(emitter, silent = false, skip = []) {
        if(emitter.isActive) {
            this.activeMember = emitter.id;
        }
    }

    /**
     * Deactivate handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onDeactivate(emitter, silent = false, skip = []) {
        if(!emitter.isActive) {
            this.activeMember = null;
        }
    }

    onDestroy(emitter, silent = false, skip = []){
        if(emitter.id === this.defaultMember) {
            this.defaultMember = null;
        }

        if(emitter.id === this.activeMember) {
            this.activeMember = null;
            this.value = null;
            this.members.forEach(id => {
                if(id === emitter.id || !this.container.has(id)) {
                    return;
                }

                let node = this.container.get(id);
                if(skip.indexOf(node) !== -1 || skip.indexOf(id) !== -1) {
                    return;
                }

                node.mutate(this.value, false, true, { emittedFrom: 'group' });
            });
        }
    }

    /**
     * Reset handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onReset(emitter, silent = false, skip = []) {
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            let node = this.container.get(id);
            if(node === emitter || skip.indexOf(node) !== -1 || skip.indexOf(id) !== -1 ) {
                return;
            }

            node.reset(silent, { emittedFrom: 'group' });
        });
    }

    /**
     * Activate handler
     *
     * @param {Model} emitter
     * @param {boolean} silent
     * @param {Array<Model>} skip
     */
    onClear(emitter, silent = false, skip = []) {
        this.members.forEach(id => {
            if(!this.container.has(id)) {
                return;
            }

            let node = this.container.get(id);
            if(node === emitter || skip.indexOf(node) !== -1 || skip.indexOf(id) !== -1 ) {
                return;
            }

            node.clear(silent, { emittedFrom: 'group' });
        });
    }

    /**
     * Get deafult group member
     *
     * @returns {Model}
     */
    getDefault() {
        if(null !== this.defaultMember && this.container.has(this.defaultMember)) {
            return this.container.get(this.defaultMember);
        }

        for (let index = 0; index < this.members.length; index++) {
            const member = this.members[index];
            if(this.container.has(member)){
                let model = this.container.get(member);
                if(model.isDefault) {
                    this.defaultMember = member;

                    return model;
                }
            }
        }

        return null;
    }

    /**
     * @inheritDoc
     */
    getActive() {
        if(null !== this.activeMember && this.container.has(this.activeMember)) {
            return [this.container.get(this.activeMember)];
        }

        for (let index = 0; index < this.members.length; index++) {
            const member = this.members[index];
            if(this.container.has(member)){
                let model = this.container.get(member);
                if(model.isActive) {
                    this.activeMember = model.id;

                    return [model];
                }
            }
        }

        this.activeMember = null;

        return [];
    }

    toData() {
        for (let index = 0; index < this.members.length; index++) {
            if(!this.container.has(this.members[index])) {
                continue;
            }

            let node = this.container.get(this.members[index]);
            if(!node.isActive) {
                continue;
            }

            return [{
                name: node.name,
                group: node.group,
                value: node.value,
            }]
        }

        return [];
    }
}