import { Observer } from "./Observer";
import { Subject } from "./Subject";
import { Config } from "./Config";

export class Service extends Observer {

    config;

    constructor(config = null) {
        super();

        if(null !== config) {
            if(config instanceof Config) {
                // Making a copy of config
                this.config = new Config(config.entries);
            } else {
                this.config = new Config(config);
            }
        } else {
            this.config = new Config();
        }

        this.registerEvents();
    }

    get name() {
        throw new TypeError("This method must be overriden in children");

        return null;
    }

    /**
     * Watch provided set of nodes
     *
     * @param { Map<string, Subject> } nodes
     */
    watch(nodes) {
        if(nodes instanceof Map) {
            for (const [key, node] of nodes) {
                if(node instanceof Subject) {
                    node.subscribe(this);
                }
            }
        } else if(nodes instanceof Subject) {
            nodes.subscribe(this);
        }

        return this;
	}

    destroy() {
        if(null !== this.events) {
            this.off();
        }

        this.events = null;
        this.config = null;

        return this;
    }

    /**
     * Registers a default set of events
     */
    registerEvents() {
	}
}