export const uppercaseFirst = function(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

export const camelCase = function(string) {
    return string.replace(/(?:^\w|[A-Z]|\b\w)/g, function(letter, index) {
        return index == 0 ? letter.toLowerCase() : letter.toUpperCase();
    }).replace(/[\s-]+/g, '');
};

export const normalizeBoolean = function(value) {
    if(typeof value === 'string') {
        value = value.toLowerCase();
        if(!isNaN(parseFloat(value)) && isFinite(value)) {
            return "1" === value;
        }
        if("true" === value || "false" === value) {
            return "true" === value;
        }

        return Boolean(value);
    } else {
        return Boolean(~~value);
    }
}

export const attributeToBoolean = function(value) {
    if('' === value) {
        return true;
    }

    return normalizeBoolean(value);
}

export const defineProperties = function (target, props) {
	for (var i = 0; i < props.length; i++) {
		var descriptor = props[i];
		descriptor.enumerable = descriptor.enumerable || false;
		descriptor.configurable = true;
		if ("value" in descriptor) {
			descriptor.writable = true;
		}

		Object.defineProperty(target, descriptor.key, descriptor);
	}
}
