import { Model } from "./Model";
import * as KnownTypes from "./registry";
import { uppercaseFirst, camelCase } from "../helpers";

export class Types {
    customTypes = {};

    constructor(types = []) {
        if(types.length) {
            for (let index = 0; index < types.length; index++) {
                this.use(types[index]);
            }
        }
    }

    find(name) {
        return this.customTypes.hasOwnProperty(name)
            ? this.customTypes[name]
            : (KnownTypes[name] || null);
    }

    has(name) {
        if(this.customTypes.hasOwnProperty(name)) {
            return true;
        }

        return typeof  KnownTypes[name] !== 'undefined' && null !==  KnownTypes[name];
    }

    use(custom) {
		if(!(custom.prototype instanceof Model)) {
            throw new TypeError("Cannot register new type - it must be instance of Model");
        }

        this.customTypes[uppercaseFirst(camelCase(custom.name))] = custom;
	}

    findAnyOf(...names){
        for (let i = 0; i < names.length; i++) {
            let name = names[i];
            if(null === name || '' === name || !this.has(name)) {
                continue;
            }

            return this.find(name);
        }

        return null;
    }
}