import { Group } from "./Group";
import * as KnownGroups from "./registry";
import { uppercaseFirst, camelCase, defineProperties } from "../helpers";

export class Types {
    customTypes = {};

    constructor(types = []) {
        if(types.length) {
            for (let index = 0; index < types.length; index++) {
                this.use(types[index]);
            }
        }
    }

    find(name) {
        return this.customTypes.hasOwnProperty(name)
            ? this.customTypes[name]
            : (KnownGroups[name] || null);
    }

    has(name) {
        if(this.customTypes.hasOwnProperty(name)) {
            return true;
        }

        return typeof KnownGroups[name] !== 'undefined' && null !== KnownGroups[name];
    }

    use(custom) {
		if (!(custom.prototype instanceof Group)) {
			if (!custom.hasOwnProperty('name')) {
				throw new Error("Group name is required");
			}

			let {
                name,
				type = null,
				limit = null,
				methods = null
			} = custom;

            let groupClass;
            let refer = null !== type ? uppercaseFirst(camelCase(type)) : null;

            if(null !== limit && isNaN(limit)) {
                throw new TypeError("Limit must be a number");
            }
            if(null !== refer && typeof KnownGroups[refer] === 'undefined') {
                throw new TypeError(`Unknown group "${type}"`);
            }

            if(null !== refer) {
                groupClass = this.find(refer) || Group;
            } else {
                groupClass = Group;
            }
			if (methods && Object.keys(methods).length > 0) {
                // Falling back to dinamyc group type
                defineProperties(groupClass.prototype, Object.keys(methods).map(key => {
                    if(typeof methods[key] !== 'function') {
                        return null;
                    }

                    return {
                        key: key,
                        value: methods[key],
                    }
                }).filter(descriptor => { return null !== descriptor}));
            }

            defineProperties(groupClass.prototype, [
                {
                    key: 'limit',
                    configurable: false,
                    writable: false,
                    value: limit
                }
            ]);

            this.customTypes[uppercaseFirst(camelCase(name))] = class {
                constructor() {
                    return Reflect.construct(groupClass, [name, limit]);
                }
            };
        } else {
            this.customTypes[uppercaseFirst(camelCase(custom.name))] = custom;
        }
	}

    findAnyOf(...names){
        for (let i = 0; i < names.length; i++) {
            let name = names[i];
            if(null === name || '' === name || !this.has(name)) {
                continue;
            }

            return this.find(name);
        }

        return null;
    }
}