import { Button } from "./Button";

export class Toggle extends Button {

    /**
     * @inheritDoc
     */
    static get type() {
        return 'toggle';
    }

    /**
     * @inheritDoc
     */
    async attachDOMListners() {
        if(!this.node.length) {
            return;
        }

        let self = this;
        this.node.on('click', function(event) {
            self.toggleState();
            self.synchronize();
            self.emit('update');
        });
    }
}