import { Input } from "./Input";
import { DATASET_KEYS } from "../../constants";
import { attributeToBoolean } from "../../helpers";

export class InputHidden extends Input {

    /**
     * @inheritDoc
     */
    static get type() {
        return 'inputHidden';
    }

    /**
     * @inheritDoc
     */
    async parseAttributes(){
        await Input.prototype.parseAttributes.call(this);

        let dataset = this.node[0].dataset;

        // By default value is immutable
        this.data.immutable = true;
		if (dataset.hasOwnProperty(DATASET_KEYS.immutable)) {
            this.data.immutable = attributeToBoolean(dataset[DATASET_KEYS.immutable])
		}
    }

    /**
     * @inheritDoc
     */
    async attachDOMListners() {
        if(!this.node.length) {
            return;
        }

        let self = this;
        this.node.on('change', function(event) {
            let value = $(this).val();
            let state = null !== value && '' !== value;

            self.setValue(value);
            self.setState(state)
            self.synchronize()
            self.emit('update');
        });
    }
}