import { Item } from "."

export class ListItem extends Item {

	render({ list, attributes }) {
        let displayName = list[0].displayName;
        let displayValue = list[0].displayValue;
        let displayDescription = list[0].displayDescription || null;
        if(list.length > 0 && !list[0].isSyncEnabled) {
            let values = [];
            list.forEach(i => {
                values.push(i.displayValue)
            });

            displayValue = values;
        }

        if(Array.isArray(displayValue)) {
            displayValue = displayValue.join(', ');
        }
        attributes[this.keyAttribute] = this.key;

		return (
			<li {...attributes} key={this.key}>
                <div class="view">
                    <span class="title">{displayName}</span>
                    <span class="value">{displayValue}</span>
                    { displayDescription ? <span class="description">{displayDescription}</span> : null }
					<button type="button" class="close destroy" aria-label="Close" onClick={this.destroyHandler} >
                        <span aria-hidden="true">&times;</span>
                    </button>
				</div>
			</li>
		);
	}
}