import { h, Component } from 'preact';
import { DATASET_NAMESPACE } from "../constants";

export class Button extends Component {

    constructor(props, state) {
        super(props, state);

        this.keyAttribute = `data-${DATASET_NAMESPACE}-html-button`;
        if(this.props.attributes) {
            for (const key in this.props.attributes.class) {
                if (this.props.attributes.class.hasOwnProperty(key)) {
                    let attribute = this.props.attributes.class[key];
                    if(Array.isArray(attribute)) {
                        attribute = attribute.join(' ');
                    }
                }
            }
        }
    }

    click = e => {
        if(null !== this.props.action && typeof this.props.onClick === 'function') {
            const { onUpdate, onReset, onClear } = this.props;

            this.props.onClick.call(this.base, e, { update: onUpdate, reset: onReset, clear: onClear });
        }
    }

	render({type, id, text, title, attributes}) {
        attributes[this.keyAttribute] = '';

        return h(type || 'button', {id: id || null, title: title, ...attributes, onClick: this.click}, text);
	}
}