import { Model } from "./Model";
import { uppercaseFirst, camelCase } from "../helpers";
import { DATASET_NAMESPACE } from "../constants";

export class Factory {

    config;
    types;

    constructor(config, types) {
        this.types = types;
        this.config = config;
    }

    /**
     * Resolve type of the model
     *
     * @param {JQuery} node
     */
    resolveNodeType(node) {
        let type = null;
        let tagName = '';
        let tagType = '';
        let explicitType = node.data(`${DATASET_NAMESPACE}-type`) || null;
        if(null === explicitType) {
            tagName = node[0].tagName || node.prop('tagName');
            tagType = node.prop('type') || '';

            tagName = uppercaseFirst(camelCase(tagName.toLowerCase()));
            if('' !== tagType) {
                tagType = uppercaseFirst(camelCase(tagType));
            }

            type = `${tagName}${tagType}`;
        } else {
            type = uppercaseFirst(camelCase(explicitType));
        }

        return { type, tagName, tagType };
    }

    /**
     * Make a model for node
     *
     * @param {JQuery} node
     */
    async make(node) {
        let { type, tagName, tagType } = this.resolveNodeType(node);
        let options = {
            secure: this.config.get('secureBinding'),
            clearShared: this.config.get('clearSharedAttributes'),
            box: this.config.get('boxKey', ''),
        };
        let model = null;
        let knownType = this.types.findAnyOf(type, tagType, tagName);
        if(null !== knownType) {
            model = Reflect.construct(knownType, [node, options]);
        } else {
            console.warn(`Type "${type}" is not supported, please use known type or define new one`);

            model = new Model(node, options);
        }

        return model;
    }

    /**
     * Process a batch of nodes
     *
     * @param {Array<JQuery>} nodes
     */
    async makeBatch(nodes) {
        let container = new Map();
        for (let index = 0; index < nodes.length; index++) {
            let node = nodes[index];
            let Model = await this.make(node);

            container.set(Model.id, Model);
        }

        return container;
    }
}