import { Model } from "../Model";
import { DATASET_KEYS } from "../../constants";

export class Input extends Model {

    /**
     * @inheritDoc
     */
    static get type() {
        return 'input';
    }

    /**
     * @inheritDoc
     */
    async parseAttributes(){
        await Model.prototype.parseAttributes.call(this);

        let node = this.node;
		let element = node[0];
        let dataset = element.dataset || {};

        this.data.name = this.node.prop('name') || this.data.name || null;

        let value = this.parseValue();
        if(null !== value && '' !== value) {
            if(!dataset.hasOwnProperty(DATASET_KEYS.active)) {
                this.data.active = true;
            }
        }
    }

    /**
     * @inheritDoc
     */
    async attachDOMListners() {
        if(!this.node.length) {
            return;
        }

        let self = this;
        this.node.on('change', function(event) {
            let value = $(this).val();
            let state = null !== value && '' !== value;

            self.setValue(value);
            self.setState(state);
            self.synchronize();
            self.emit('update');
        });
    }

    /**
     * @inheritDoc
     */
    parseValue(){
        let node = this.node;
        let value = this.node.val() || null;
        if(null === value) {
            value = node[0].dataset[DATASET_KEYS.value] || null;
        }

        return value;
    }

    /**
     * @inheritDoc
     */
    mutate(value, state, silent = false, ...args) {
        const emitionState = args[0] || {};
        silent = Boolean(silent);
        state = Boolean(state);


        this.setState(state, false, emitionState);
        this.setValue(value, emitionState);
        this.setNodeValue(value);
        if(!silent) {
            this.node.trigger('change');
        }

        return this;
    }
}