import { Observer, Subject } from "../common";
import { Model } from "../nodes";

export class Container {

    nodes = new Map();

    get entries() {
        return this.nodes.entries();
    }

    get values() {
        return this.nodes.values();
    }

    get keys() {
        return this.nodes.keys();
    }

    /**
     * Determines if container contains node
     *
     * @param {Model} node
     *
     * @returns {Boolean}
     */
    contains(node) {
        return this.nodes.has(node.id);
    }

    /**
     * Determines if container has node
     *
     * @param {string} id
     *
     * @returns {boolean}
     */
    has(id) {
        return this.nodes.has(id);
    }

    /**
     * Get the node by id
     *
     * @param {String} id
     *
     * @returns {Model}
     */
    get(id) {
        return this.nodes.get(id) || null;
    }

    /**
     * Bind a set of nodes to container
     *
     * @param { {[key: String]: Model} | Map<String, Model> | Model } nodes
     *
     * @returns {Container}
     */
    bind(nodes) {
        if(nodes instanceof Model) {
            this.nodes.set(nodes.id, nodes);
        } else {
            try {
                for (const [key, node] of nodes) {
                    this.nodes.set(key, node);
                }
            } catch (error) {
                console.log(error);
            }
        }

        return this;
    }

    /**
     * Drops node from the register
     *
     * @param {DataNode} node
     *
     * @returns {Container}
     */
    unbind(node) {
        let id = node.id;
        if(this.nodes.has(id)) {
            this.nodes.delete(id);
        }

        return this;
    }

    /**
     * Clears the container
     */
    clear() {
        let nodes = Array.from(this.values);
        for (let index = 0; index < nodes.length; index++) {
            nodes[index].destroy();
        }

        this.nodes.clear();
	}

    /**
     * Remove orphaned nodes
     */
    removeOrphans() {
        let nodes = Array.from(this.values);

        for (let index = 0; index < nodes.length; index++) {
            let node = nodes[index];
            if(!node.isExistsInDOM) {
                node.destroy();
                this.nodes.delete(node.id);
            }
        }
    }
}