import jQuery from 'jquery';
import * as StateBox from './lib';

(function ($, StateBox) {
	$.stateBox = function (options = {}) {
        let config = {};
        let stateBox;
        let { selector, groups = [], types = [], html = {} } = options;
        const { buttons = [] } = html;
        if(!selector) {
            throw new TypeError('Invalid selector provided - string expected, got ' + typeof selector);
        }

        // Remove buttons pointer
        delete options.html.buttons;

        config = $.extend(true, {}, StateBox.constants.DEFAULTS, options);
        if(config.hasOwnProperty('groups')) {
            delete config.groups;
        }
        if(config.hasOwnProperty('types')) {
            delete config.types;
        }

        // If live update is enabled, remove update button as redundant
        if(config.html.liveUpdate) {
            config.html.buttons.splice(0, 1);
        }

        // Replacing the default buttons with
        if(buttons.length) {
            if('append' === config.html.buttonsMode) {
                config.html.buttons = config.html.buttons.concat(buttons);
            } else {
                config.html.buttons = buttons;
            }
        }

        stateBox = new StateBox.Box(selector, config.container || null, types, groups, config);
        let nodes = $(selector);
        let nodeList = [];
        nodes.each((i, node) => {
            nodeList.push($(node));
        });

        return stateBox.register(nodeList).then(async (box) => {
            return await box.build();
        });
    };
    $.stateBox.Model = StateBox.Model;
    $.stateBox.Types = StateBox.Nodes.knownTypes;
    $.stateBox.Group = StateBox.Groups.Group;
    $.stateBox.Groups = StateBox.Groups.knownGroups;
    $.stateBox.Components = StateBox.Components;
    $.stateBox.Constants = StateBox.constants;
}(jQuery, StateBox));