import { Subject } from "./Subject";

/**
 * @property {Map<string:any>} events
 */
export class Observer {

    events = new Map();

    /**
     * Attach a listner for the subject's event
     *
     * @param {string} event
     * @param {Subject|Function} subject
     * @param {null|Function} listner
     */
    on(event, subject, listner = null) {
        if(!(subject instanceof Subject)) {
            if(typeof subject === 'function') {
                this.addEventListner(event, subject);
            }

            return this;
        }

        if(typeof listner !== 'function') {
            return this;
        }

        this.addEventListner(`${event}-${subject.id}`, listner);

        return this;
    }

    /**
     * Detach listners for event and subect
     *
     * @param {string|null} event
     * @param {Subject|null} subject
     */
    off(event = null, subject = null) {
        if(null === event && null === subject) {
            this.events.clear();

            return;
        }

        if(event instanceof Subject) {
            this.removeListnersForSubject(event);

            return;
        }

        if(null === subject) {
            this.removeListnersForEvent(event);

            return;
        }

        this.events.delete(`${event}-${subject.id}`);

        return;
    }

    /**
     * Notify observer about specifi changes in subject
     *
     * @param {string} event
     * @param {Subject} subject
     * @param {any[]} args
     */
    notify(event, subject, ...args) {
        let listner = null;
        if(this.events.has(`${event}-${subject.id}`)) {
            listner = this.events.get(`${event}-${subject.id}`);
        } else {
            listner = this.events.get(event) || null;
        }

        if(null !== listner) {
            listner.forEach(l => { l.call(this, subject, ...args) });
        }
    }

    /**
     * Add event listner to the storage
     *
     * @param {string} event
     * @param {Function} listner
     */
    addEventListner(event, listner) {
        if(this.events.has(event)) {
            this.events.get(event).push(listner);
        } else {
            this.events.set(event, [listner]);
        }
    }

    /**
     * Remove all handlers for provided subject
     *
     * @param {Subject} target
     */
    removeListnersForSubject(target) {
        let id = target.id;
        for (let [key, handler] of this.events) {
            if(!key.includes(id)) {
                continue;
            }

            this.events.delete(key);
        }

        return;
    }

    /**
     * Remove all handler for provided event
     *
     * @param {string} event
     */
    removeListnersForEvent(event) {
        let length = event.length;
        for (let [key, handler] of this.events) {
            if(event !== key.slice(0, length)) {
                continue;
            }

            this.events.delete(key);
        }

        return;
    }
}