import { Group } from "./Group";
import { Model } from "../nodes";
import { uppercaseFirst, camelCase } from "../helpers";

export class Container {

    list;
    types;
    items = new Map();

    get all() {
        return Array.from(this.items.values());
    }

    constructor(list, types) {
        this.list = list;
        this.types = types;
    }

    has(name) {
        if(typeof name !== 'string') {
            return false;
        }

        return this.items.has(name);
    }

    contains(group) {
        if(!(group instanceof Group)) {
            return false;
        }

        for (const [key, storedGroup] of this.items) {
            if(group === storedGroup) {
                return true;
            }
        }

        return false;
    }

    add(group) {
        if(!(group.prototype instanceof Group) && !(group instanceof Group)) {
            throw new TypeError("Instance of Group expected");
        }

        Object.defineProperty(group, 'models', {
            configurable: false,
            writable: false,
            value: this.list
        });

        this.items.set(group.name, group);

        return this;
    }

    take(group) {
        if(this.has(group)) {
            return this.items.get(group);
        }

        return null;
    }

    drop(group) {
        let name = group instanceof Group ? group.name : group;
        if(this.has(name)) {
            this.items.delete(name);
        }

        return this;
    }

    findInGroups(id) {
        let values = Array.from(this.items.values());
        let length = values.length;
        for (let index = 0; index < length; index++) {
            const group = values[index];
            if(group.has(id)) {
                return group.take(id);
            }
        }

        return null;
    }

    putInGroup(node, group) {
        let id = node instanceof Model ? node.id : node;
        let name = group;

        if(this.has(name)) {
            let group = this.take(name);

            group.add(id);
        }

        return this;
    }

    putInGroups(node) {
        let id = node instanceof Model ? node.id : node;
        let values = Array.from(this.items.values());
        let length = values.length;
        for (let index = 0; index < length; index++) {
            const group = values[index];

            group.add(id);
        }

        return this;
    }

    removeFromGroup(group, node) {
        let id = node instanceof Model ? node.id : node;
        let name = group;
        if(this.has(name)) {
            let group = this.take(name);
            if(group.has(id)) {
                group.drop(id);
            }
        }

        return this;
    }

    removeFromGroups(id) {
        let values = Array.from(this.items.values());
        let length = values.length;
        for (let index = 0; index < length; index++) {
            const group = values[index];
            if(group.has(id)) {
                group.drop(id);
            }
        }

        return this;
    }

    /**
     * Resolve group assignement of the node
     *
     * @param {Model} node
     * @param {boolean} createGroup
     */
    resolveGroup(node) {
        const groupName = node.group;

        if(null !== groupName) {
            const groupType = node.groupType;
            const groupIdentifier = null !==  groupType ? uppercaseFirst(camelCase(groupType)) : groupType;

            if(this.has(groupName)) {
                if(null === groupIdentifier) {
                    console.error(`In order to add element to group type must be defined`);

                    return;
                }

                const group = this.take(groupName);
                if(group.constructor.name !== groupIdentifier) {
                    console.warn(`Cannot redefine group for the node with id "${node.id}" - type for the group is alredy set`)
                }

                group.add(node);
            } else {
                if(null === groupIdentifier) {
                    console.error(`In order to create group type must be defined`);

                    return;
                }

                if(!this.types.has(groupIdentifier)) {
                    console.error(`Group with type ${node.groupType} is not found`);

                    return;
                }

                const group = Reflect.construct(this.types.find(groupIdentifier), [groupName]);
                this.add(group);
                group.add(node);
            }
        }
	}

    /**
     * Get active models from all groups
     *
     * @returns {Array<{[name: string]: string, [data: string]: Array<Model>}>}
     */
    getActive() {
        let data = [];
        for (const [key, group] of this.items) {
            data.push({
                name: group.name,
                data: group.getActive(),
            });
        }

        return data;
    }

    toData() {
        let data = [];
        for (const [key, group] of this.items) {
            data = data.concat(group.toData());
        }

        return data;
    }

    toArray() {
        let data = [];
        for (const [key, group] of this.items) {
            data.push({
                name: group.name,
                data: group.toArray(),
            });
        }

        return data;
    }
}