import { InputRadio } from "./InputRadio";

export class InputCheckbox extends InputRadio {

    /**
     * @inheritDoc
     */
    static get type() {
        return 'inputCheckbox';
    }

    /**
     * @inheritDoc
     */
    async attachDOMListners() {
        if(!this.node.length) {
            return;
        }

        let self = this;
        this.node.on('change', function(event) {
            self.toggleState()
            self.synchronize()
            self.emit('update');
        });
    }

    /**
     * @inheritDoc
     */
    mutate(value, state, silent = false) {
        const emitionState = args[0] || {};
        silent = Boolean(silent);
        state = Boolean(state);

        this.setState(state, false, emitionState);
        this.setValue(value, emitionState);
        this.setNodeValue(this.isActive);
        if(!silent) {
            node.trigger('change');
        }

        return this;
    }
}